<template>
  <ul class="px-10 py-5">
    <li
      class="list-none px-1 py-1 border-b border-1"
      v-for="member in data"
      :key="member.id"
    >
      <div class="flex items-center justify-between my-4">
        <div class="w-16">
          <img class="w-12 h-12 rounded-full" src="../assets/images/man.png" />
        </div>
        <div class="flex-1 pl-2 text-left">
          <div class="text-gray-700 font-semibold">
            ชื่อ: {{ member.name }} {{ member.lastName }}
          </div>
          <div class="text-gray-600 font-thin">
            ข้อมูลการแพ้ยา: {{ member.drugAllergy }}
          </div>
          <div class="font-thin">
            <span class="text-gray-600">เบอร์ติดต่อ: </span>
            <span class="text-red-400">{{ member.mobile }}</span>
          </div>
        </div>
        <div>
          <IconButton
            color="bg-green-500"
            type="edit"
            @clicked="editMember(member)"
          />

          <IconButton
            class="ml-5"
            color="bg-red-500"
            type="delete"
            @clicked="deleteMember(member)"
          />
        </div>
      </div>
    </li>
  </ul>

  <Confirm
    v-if="showConfirm"
    :title="confirmTitle"
    :message="confirmMessage"
    :icon="conditionProcess"
    btnCancel="ยกเลิก"
    btnOkay="ตกลง"
    @accepted="confirmDismiss"
  />
</template>

<script>
import IconButton from "@/components/buttons/IconButton.vue";
import Confirm from "@/components/Confirm.vue";
import { ref } from "@vue/reactivity";
import router from "@/router";

export default {
  components: { IconButton, Confirm },
  props: ["data"],
  setup() {

    const confirmTitle = ref("");
    const confirmMessage = ref("");
    const conditionProcess = ref("");

    const showConfirm = ref(false);

    const editMember = (member) => {
      confirmMessage.value = member.name + " " + member.lastName;
      confirmTitle.value = "แก้ไขข้อมูลสมาชิก";
      conditionProcess.value = "edit";
      showConfirm.value = true;
    };

    const deleteMember = (member) => {
      confirmMessage.value = member.name + " " + member.lastName;
      confirmTitle.value = "ลบข้อมูลสมาชิก";
      conditionProcess.value = "delete";
      showConfirm.value = true;
    };

    const confirmDismiss = (res) => {
      showConfirm.value = false;
      console.log(res);
      if (res) {
        if (conditionProcess.value == "edit") {
          router.push({ name: "Home" });
        } else if (conditionProcess.value == "delete") {
          router.push({ name: "Stock" });
        }
      }
    };

    return {
      editMember,
      deleteMember,
      showConfirm,
      confirmDismiss,
      confirmTitle,
      confirmMessage,
      conditionProcess,
    };
  },
};
</script>

