<template>
  <NavBar />

  <div class="py-8 px-10">
    <div class="md:grid md:grid-cols-5 md:gap-6">
      <div class="md:col-span-1">
        <div class="px-4 sm:px-0">
          <h3 class="text-lg font-medium leading-6 text-gray-900">ระสมาชิก</h3>
          <p class="mt-1 text-sm text-gray-600">
            แสดงรายชื่อสมาชิกในระบบ
          </p>
          <AppButton
            color="bg-green-600"
            text="เพิ่มสมาชิก"
            @click="AddMember"
          />
        </div>
      </div>
      <div class="md:col-span-4">
        <div v-if="loading && members && members.length">
          <SearchBar
            v-model="keywordSearch"
            placeHolder="ค้นหาสมาชิก"
            @doSearch="searchMember"
            @doClear="clearSearch"
          />
          <ListMember :data="members" />
          <Pagination />
        </div>
        <div v-if="loading && members.length == 0">
          ไม่พบข้อมูลสมาชิกในระบบ
        </div>
        <p v-if="!loading">
          Still loading...
        </p>
        <p v-if="error">{{ error }}</p>
      </div>
    </div>
  </div>
</template>

<script>
import NavBar from "@/components/NavBar";
import AppButton from "@/components/Button";
import router from "@/router";
import { ref, onMounted } from "vue";
import ListMember from "@/components/ListMember";
import Pagination from "@/components/Pagination";
import SearchBar from "@/components/SearchBar";

export default {
  name: "Member",
  components: { NavBar, AppButton, ListMember, Pagination, SearchBar },
  setup() {
    const members = ref([]);
    const loading = ref(true);
    const error = ref(null);
    const keywordSearch = ref("");

    onMounted(() => {
      fetch(" http://localhost:3000/members")
        .then((res) => res.json())
        .then((data) => {
          members.value = data;
          console.log(members.value);
          loading.value = true;
        })
        .catch((err) => console.log(err.message));
    });

    const AddMember = () => {
      console.log("AddMember");
      router.push("/add-member");
    };

    const searchMember = () => {
      console.log(keywordSearch.value);
    };

    const clearSearch = () => {
      console.log("clear search");
    };

    return {
      AddMember,
      members,
      loading,
      error,
      keywordSearch,
      searchMember,
      clearSearch,
    };
  },
};
</script>
